.progress-bar{
    position: relative;
    width: 100%;
    height: 0.3rem;
    border-radius: 20px;
    border: 1px solid rgb(255, 255, 255, 0.2);
    background-color: rgb(255, 255, 255, 0.3);
    overflow: hidden;

    .progress-bar-loader{
        position: absolute;
        height: 100%;
        left: 0;
        transition: width 0.3s;
    }
}